html {
    font-family: 'Roboto';
    font-size: 62.5%;
	line-height: auto;
	color: #404040;
	-webkit-overflow-scrolling: touch;
}

body {
	margin: 0px;
    color: #14171a;
    font-size: 1.6rem;
	background-color: #f5f5f5;
}

header {
	position: relative;
	max-width: 576px;
	margin: auto;
	/*background-color: blue;*/
}

h1, h2, h3, h4, h5, h6 { margin: 0px; }

*,
*::after,
*::before {
    margin: 0px;
    padding: 0px;
    list-style: none;
    box-sizing:border-box;
    position: relative;
}

.container {

}

.pages {
	position: relative;
	max-width: 576px;
	margin: auto;
	padding: 16px;
}

a {
	text-decoration: none;
}

a:focus,
a:not([href]):not([tabindex]):hover,
a:not([href]):not([tabindex]):focus {
    text-decoration: none;
    outline: none;
    cursor: pointer;
}

.cutter {
	right: 0px;
	top: 0px;
	position: absolute;
	width: 100%;
	z-index: -1;
	transition: all 0.5s cubic-bezier(.25,.8,.25,1);
	background-image: linear-gradient(150deg, #e84118, #b31217);
}

.cutter.diagonal {
	height: 45vh;
	clip-path: polygon(0 0, 100% 0, 100% 50%, 0 100%);
}

.cutter.flat {
	height: 60px;
	background-image: linear-gradient(150deg, #e84118, #e84118);
}

.green {
	color: #44bd32;
}

.blue {
	color: #00a8ff;
}

.clear:after {
	content: "";
	display: table;
	clear:both;
}