.questions {
	position: relative;
}

.next, .previous, .finish {
	display: block;
	padding: 8px 16px;
	background-color: #00a8ff;
	float: left;
	color: #fff;
	cursor: pointer;
}

.finish {
	display: none;
	float: none;
	margin: 16px auto;
    width: 80px;
    text-align: center;
}

.next {
	float: right;
}