.hamburger {
	width: 72px;
	height: 60px;
	display: block;
	/*background: orange;*/
	position: relative;
	overflow: hidden;
	float: left;
}

.hamburger {

}

.hamburger > div {
	width: calc(100% - 32px);
	height: 4px;
	background-color: #ffffff;
	border-radius: 51px;
	position: absolute;
	left: 16px;
	opacity: 1;
	transition: all 0.5s cubic-bezier(.25,.8,.25,1);
}

.hamburger > div:nth-child(1) {
	top:16px;
}

.hamburger > div:nth-child(2) {
	top: 28px;
}

.hamburger > div:nth-child(3) {
	top: 40px;
}


.hamburger.cross > div:nth-child(3) {
	top:28px;
	transform:rotate(-45deg);
}

.hamburger.cross > div:nth-child(2) {
	top:28px;
	transform:rotate(45deg);
}

.hamburger.cross > div:nth-child(1) {
	top:-16px;
	opacity: 0;
}